.alert {
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
}
.alert-info {
  color: #2020a0;
  background-color: #e0e0ff;
}
.alert-danger {
  color: #a02020;
  background-color: #ffe0e0;
}
.alert-success {
  color: #20a020;
  background-color: #eeffe0;
}
/* Form */
.form {
  max-width: 60rem;
  margin: 0 auto;
}
.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.form label {
  margin: 1rem 0;
}
/* Dropdown */
.dropdown {
  display: inline-block;
  position: relative;
}
.dropdown-content {
  position: absolute;
  display: none;
  right: 0;
  min-width: 12rem;
  padding: 1rem;
  z-index: 1;
  background-color: #203040;
  margin: 0;
  margin-top: 0.4rem;
  border-radius: 0.5rem;
}
.dropdown:hover .dropdown-content {
  display: block;
}
/* Checkout Steps */
.checkout-steps > div {
  border-top: 0.3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex: 1;
  padding: 1rem;
  font-weight: bold;
}
.checkout-steps > div.active {
  border-top-color: #f08000;
  color: #f08000;
}
/* Table */
.table {
  width: 100%;
  border-collapse: collapse;
}
.table tbody tr:nth-of-type(odd) {
  background-color: #f4f4f4;
}
.table td,
.table th {
  text-align: left;
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem;
}
.table button {
  margin: 0 0.2rem;
}
/* Pagination */

.pagination-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.pagination-container > div {
  padding-top: 10px;
}

.paginate-ctn {
  display: flex;
}

.round-effect {
  color: #FFFFFF;
  background-color: #26272b;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  padding-left: 3px;
  padding-right: 3px;
  padding-top:3px;
  border-radius: 50%;
  height: 20px;
  width:20px;
  margin: 5px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 2px 4px rgba(0, 0, 0, 0.23);
  text-decoration: none;
}

.round-effect:hover {
  text-decoration: none;
  background: #2472d3;
  color: #FFF;
}

.active {
  background: #2472d3;
  color: #FFF;
  -webkit-box-shadow: 0 2px 9px 2px rgba(0, 0, 0, 0.77);
  -moz-box-shadow: 0 2px 9px 2px rgba(0, 0, 0, 0.77);
  box-shadow: 0 2px 9px 2px rgba(0, 0, 0, 0.77);
}
.liq_pay_pay {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

}
.search_box {
  display: flex;
  justify-content: center;
}
/* Filter */
.filter {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  border-bottom: 0.1rem #c0c0c0 solid;
}
.filter div {
  flex: 1;
}